import { Directive, Input, Output, EventEmitter} from '@angular/core';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import {toBoolean} from '../util/util';


export class NglPick {

  selected: any;
  values = new BehaviorSubject(null);
  isMultiple = false;

   set setSelected(selected: string) {
    this.selected = selected;
    this.ngAfterContentInit();
  }

   nglPickActiveClass: string;

   nglPickChange = new EventEmitter();
   nglOptionDestroyed = new EventEmitter();

   set setIsMultiple(isMultiple: any) {
    this.isMultiple = toBoolean(isMultiple);
  }

  ngAfterContentInit() {
    this.values.next(this.selected);
  }

  selectOption(value: any) {
    let next: any;
    if (this.isMultiple) {
      if (Array.isArray(this.selected)) {
        // Remove if already there or add to selection
        const index = this.selected.indexOf(value);
        next = index > -1
                ? [...this.selected.slice(0, index), ...this.selected.slice(index + 1)]
                : [...this.selected, value];
      } else {
        next = Object.assign({}, this.selected, {[value]: !this.selected[value]});
      }
    } else {
      next = value;
    }

    this.nglPickChange.emit(next);
  }

  optionRemoved(value: any) {
    if (this.isMultiple && !this.selected) return;

    let emit: boolean;

    if (this.isMultiple) {
      emit = Array.isArray(this.selected) ? this.selected.indexOf(value) > -1 : !!this.selected[value];
    } else {
      emit = this.selected === value;
    }

    if (emit) {
      setTimeout(() => this.nglOptionDestroyed.emit(value));
    }
  }
static decorators: DecoratorInvocation[] = [
{ type: Directive, args: [{
  selector: '[nglPick]',
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'setSelected': [{ type: Input, args: ['nglPick', ] },],
'nglPickActiveClass': [{ type: Input },],
'nglPickChange': [{ type: Output },],
'nglOptionDestroyed': [{ type: Output },],
'setIsMultiple': [{ type: Input, args: ['nglPickMultiple', ] },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
